@import url(https://fonts.googleapis.com/css2?family=Philosopher&display=swap);
body {
    font-family: 'Roboto', sans-serif;
    /* font-weight: 400; */
    background-image: radial-gradient(#ddd 1px,transparent 0),radial-gradient(#ddd 1px,transparent 0);
    background-position: 0 0,25px 25px;
    background-attachment: fixed;
    background-size: 50px 50px;
    padding: 0;
    margin: 0;
    cursor: none;
}

ul {
    padding: 0;
    margin: 0;
}

/* My name section */
h1 {
    padding-top: 1rem;
    text-align: center;
    letter-spacing: 1px;
}

/* About me section */

.about-me {
    width: 100%;
    margin: 1rem auto;
}
.about-me__row {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: center;
            align-items: center;
}



.about-me__col-description {
    width: 80%;
    line-height: 2rem;
}
.about-me__col-image {
   width: 80%;
   text-align: center;
}
.about-me__row p {
   letter-spacing: 1px;
   font-weight: lighter;
}

.about-me__col-image__profile-image {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    box-shadow: 0px 0px 100px rgb(83, 76, 76);
}

@media screen and (min-width: 800px) {
    .about-me {
        width: 70%;
        margin: 1rem auto;
    }
    .about-me__row {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        -webkit-flex: 1 1;
                flex: 1 1;
        -webkit-align-items: center;
                align-items: center;
    }
    .about-me__col-description {
        width: 70%;
        line-height: 2rem;
    }
    .about-me__row p {
        letter-spacing: 1px;
        font-weight: lighter;
        
     }
     .about-me__col-image {
        width: 10%;
        padding-right: 10rem;
     
     }
    .about-me__col-image__profile-image {
        width: 250px;
        height: 250px;
        border-radius: 50%;
    
    }
}


  /* Skills section */
.skills {
    width: 100%;
}
.skills__container-wrapper {
    width: 70%;
    padding: 10px;
    margin: 1rem auto;
}

.skills__container-wrapper__skills-flex {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    /* width: 80%; */
    padding: 20px 0px;     
}

.skills__container-wrapper__skills-flex__icon-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-right: 5px;      
}
.skills__container-wrapper__skills-flex__icon-container span {
    font-weight: bold;
}

@media screen and (min-width: 800px) { 
    .skills__container-wrapper__skills-flex {
        -webkit-justify-content: space-between;
                justify-content: space-between;
        width: 80%;   
    }

    .skills__container-wrapper__skills-flex__icon-container  {
        margin-right: 0;
    }
}

a, a:hover{
    color: black;
}

.title{
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 4rem;
    color: rgb(52, 51, 51);
    text-shadow: 12px 6px 4px #6e6c6c;
}
.titlename{
  text-align: center;
  font-family: 'Philosopher';
  font-size: 3rem;
  color: white;
  text-shadow: 0px 2px 7px #000000;
}

strong{
    font-weight: bold;
}

.download-resume {
    font-size: large;
    font-family: Verdana;
}

.download-resume a {
    text-decoration: none;
    text-shadow: 0px 2px 7px #000000;
}
/*  */
.card-container-wrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.card-container-wrapper__card{
  height: 280px;
  width: 250px;
  background-color: black;
  margin: 5%;
  border-radius: 10px;
  transition: 1s all;
  border: 1px solid black;

}

.card-container-wrapper__card:hover{
  box-shadow: 0px 10px 52px -13px rgba(255,255,255,0.75);
}

.card-container-wrapper__card__image-container__image {
  max-width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;


}
.card-container-wrapper__card__description {
    color: cornsilk;
    /* margin-left: 8px; */
    padding: 6px;
    letter-spacing: 1px;
    font-size: 12px;
    
}
.card-container-wrapper__card__icons-container-wrapper__icon {
  color: white!important;
}
.card-container-wrapper__card__icons-container-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;

}
.card-container-wrapper__card__icons-container-wrapper a {
  padding-right: 1rem;
}

@media screen and (min-width: 800px) {
  .card-container-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
  }
  .card-container-wrapper__card {
    height: 300px;
    width: 300px;
  
  }
}

.seperate {
  margin: 10px;
}

.zoom {
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

.zoom:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

#shadow:hover {
  border-radius: 50%;
  box-shadow: 0px 0px 30px rgb(83, 76, 76);
} 
