  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 4rem;
  color: rgb(52, 51, 51);
  text-shadow: 12px 6px 4px #6e6c6c;
}
.footer-container-wrapper__footer-role {
    text-align: center;
    padding-top: 5rem;
}
.footer-container-wrapper__footer-social-links {
    text-align: center;
    padding-bottom: 3rem;

}
.footer-container-wrapper__footer-social-links a {
    padding-right: 2rem;
}

.footer-container-wrapper__footer-social-links a img{
